body {
  margin: 0;
  padding: 0;
}
*{
  margin: 0;
  padding: 0;
}
body {
  background-image: url(/kris-build/static/media/bg-body.ade5996c.jpg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.flex-box-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    padding-top: 10px;
    padding-bottom: 10px; 
    z-index: 500;
}
.logo {
    width: 120px;
}

.logo img {
    max-width: 100%;
}
nav.flex-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
nav.flex-nav ul{
    margin: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
nav.flex-nav ul li{
    list-style: none;
    margin-left: 30px;
}
nav.flex-nav ul li a {
    text-transform: uppercase;
    color: #fff;
    font-weight: 900;
    text-shadow: 0px 0px 3px rgb(255, 145, 0);
    font-size: 18px;
    transition: all 0.3s;
}
nav.flex-nav ul li a:hover {
    text-decoration: none;
    color: #ffdb4f;
}
nav.flex-nav .list-box{
    display: flex;
    justify-content: center;
    align-items: center;
}
nav.flex-nav .social-box {
    margin-left: 40px;
}
nav.flex-nav .social-box li {
    margin-left: 10px;
}
nav.flex-nav .social-box li a  {
    font-size: 25px;
}
nav.flex-nav .flex-box .btn {
    display: none;
}

@media (max-width:769px) {
 nav.flex-nav .flex-box ul{
        display: none;
 }
 nav.flex-nav .flex-box .btn {
        display: inline-block;
 }
}
main {
    width: 100%;
}
.home {
    /* background: rgb(174, 221, 160); */
   min-height: 100vh;
}
.col-lg-7.flex-box-center, .col-lg-5.flex-box-center {
    height: 100vh;
}

.home figure  {
    text-align: right;
    width: 90%;
    position: relative;
}
.home figure img {
    max-width: 100%;
}
.mg-top-30{
    margin-top: 30px;
}

.home figure .banner-absolute{
    position:absolute;
    bottom:60px ;
    left: 0;
    right: 0;
    width: 100%;
    text-align: center;
}
.home figure .banner-absolute img {
    max-width: 75%;
    -webkit-animation: beating 0.8s infinite;
            animation: beating 0.8s infinite;
    -webkit-transform: scale(1);
            transform: scale(1);

}
.home .action figure {
    text-align: left;
    width: 100%;
}
.home .action figure img {
    max-width: 65%;
}
.btn-dale {
    width: 65%;
    text-align: center;
}
.btn-dale img {
    max-width: 70%;
}
.home .action .flex-box-center {
    justify-content: flex-start;
}


@media (max-width:769px) {
    .col-lg-7.flex-box-center .col-lg-5.flex-box-center {
        height: auto;
        padding-top: 50px;
    }
}

@-webkit-keyframes beating {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1)
    }
    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2)        
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1)
        
    }
}

@keyframes beating {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1)
    }
    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2)        
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1)
        
    }
}
.ingresaTuCodigo{
    /* background: rgb(160, 207, 221); */
}
.ingresaTuCodigo .flex-box{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    min-height: 100vh;
}
.ingresaTuCodigo figure  {
    width: 280px;
    margin: 0 auto;
}
.ingresaTuCodigo figure img {
    margin-bottom: 25px;
}
.ingresaTuCodigo .input{
    width: 430px;
}
.ingresaTuCodigo input {
    width: 100%;
    border-radius: 55px;
    border: 3px solid orange;
    padding:15px 30px;
    background-color: #ffea98; 
    text-align: center;
    color: orange;
    font-size: 25px;
    font-weight: 900;
}
.ingresaTuCodigo .button{
    width: 100%;
    text-align: center;
    padding-top: 25px;
    padding-bottom: 25px;
    
}
.ingresaTuCodigo figure img {
    max-width: 100%;
}
.ingresaTuCodigo .button .send-code {
    width: 220px;
    height: 80px;
    text-align: center;
    color: rgb(255, 238, 0);
    background-image: url(data:image/png;base64,);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    font-weight: 900;
    font-size: 25px;
    cursor: pointer;
}
.comoParticipar {
  /* background: rgb(221, 160, 211);  */
}

.comoParticipar .flex-box{
